import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service'
import { GroupEvalInfoBean } from './../../bean/custom-group.bean'
import { Observable } from 'rxjs/Rx';
import * as _ from 'lodash';
import { Message, ConfirmationService, ConfirmDialogModule, SelectItem } from 'primeng/primeng'
import { ActivatedRoute, Params, Router } from '@angular/router';
import { GroupAdminInfoBean } from './bean/GroupAdminInfo.bean';
import { customInfoBean } from './bean/customInfo.bean';
import { CustGroupListBean } from './../../bean/custom-group.bean';
import { Validators, FormControl, AbstractControl, FormGroup, FormBuilder } from '@angular/forms';
import { checkCard, checkTel } from 'app/@uisftech/common/service/validator';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';


@Component({
  selector: 'app-custom-evaluate',
  templateUrl: './custom-evaluate.component.html',
  styleUrls: ['./custom-evaluate.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class CustomEvaluateComponent implements OnInit {

  //
  tableMesg: any = tableMessage;
  // 
  customEvaluate: string = "";
  evaluatedCustomer: string = "";
  evaluateInformation: string = "";
  arr: any;
  cars: any;
  // bean
  groupEvalInfoBean: GroupEvalInfoBean = new GroupEvalInfoBean();//
  groupAdminInfoBean: GroupAdminInfoBean = new GroupAdminInfoBean();//
  custGroupListBean: CustGroupListBean = new CustGroupListBean();//

  // 
  // 
  msgs: Message[] = [];

  // 
  custGroupListDisplay: boolean = false;
  inVal: any = [];
  // 
  groupId: any = '';//
  groupName: any = '';//
  custNo: any = '';//
  custGroupView: any = '';//
  // 
  isCustGroupView: boolean = false;
  infoEditFlag: boolean = true;
  //
  myForm: FormGroup;
  submitted: boolean;
  genders: SelectItem[];
  description: string;

  agentCreditFlag: any;
  systemCreditFlag: any;
  pageNum: string;
  isNoValue: any = [
    { label: '', value: '' },
    { label: '', value: '1' },
    { label: '', value: '0' },
  ]

  code: any;
  custNoTemp: string;//
  groupHealthFlag: any;// = GroupHealthFlag;// 
  groupCreditEval: any;// = GroupCreditEval;// 
  groupSourceOfIncome: any;// = GroupSourceOfIncome;// 
  groupFamilyStableFlag: any;// = GroupFamilyStableFlags;// 
  groupBadHabits: any;// = GroupBadHabits;// 
  groupCarFlag: any;// = GroupCarFlag;// 
  groupIncomeLevel: any;// = GroupIncomeLevel; // 
  groupOfferLines: any;// = GroupOfferLinese;// 
  idenType: any;// = CustomGroupIdenType;//
  //
  CLIENT_TYPE: any = []
  MANAGE_ITEM: any = []
  LICENCE_YEARS: any = []
  HOUSE_STATUS: any = []
  ANNUAL_INCOME: any = []
  FAMILY_NET_AST_RANK: any = []
  RISK_ADVISE: any = []
  yushouxin: boolean = true;
  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public confirmationService: ConfirmationService,
    public router: Router,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues() //
    //,
    this.groupHealthFlag = this.code['GroupHealthFlag'];
    this.groupCreditEval = this.code['GroupCreditEval'];
    this.groupSourceOfIncome = this.code['GroupSourceOfIncome']

    this.groupFamilyStableFlag = this.code['GroupFamilyStableFlags'];
    this.groupBadHabits = this.code['GroupBadHabits'];
    this.groupCarFlag = this.code['GroupCarFlag']

    this.groupIncomeLevel = this.code['GroupIncomeLevel'];
    this.groupOfferLines = this.code['GroupOfferLinese'];
    this.idenType = this.code['CustomGroupIdenType']
    this.CLIENT_TYPE = this.code['CLIENT_TYPE'];
    this.MANAGE_ITEM = this.code['MANAGE_ITEM'];
    this.LICENCE_YEARS = this.code['LICENCE_YEARS']
    this.HOUSE_STATUS = this.code['HOUSE_STATUS'];
    this.ANNUAL_INCOME = this.code['ANNUAL_INCOME'];
    this.FAMILY_NET_AST_RANK = this.code['FAMILY_NET_AST_RANK']
    this.RISK_ADVISE = this.code['RISK_ADVISE']

  }
  ngOnInit() {
    this.preSet();

    this.selectGroupEvalInfo();
  }
  //
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code
  }
  preSet() {
    // 
    // this.groupEvalInfoBean.pageNum = 1;
    // this.groupEvalInfoBean.pageSize = 10;
    // debugger
    if (this.routerInfo.snapshot.queryParams['id']) {
      this.groupId = this.routerInfo.snapshot.queryParams['groupId'];//id            
      this.groupEvalInfoBean.groupId = this.groupId;
    }
    if (this.routerInfo.snapshot.params['groupId']) {
      this.groupId = this.routerInfo.snapshot.params['groupId'];//     
    }
    if (this.routerInfo.snapshot.params['yushouxin']) {
      if (this.routerInfo.snapshot.params['yushouxin'] == '1') {
        this.yushouxin = false;
      }
    }
    // if (this.routerInfo.snapshot.params['groupName']) {
    //   this.groupName = this.routerInfo.snapshot.params['groupName'];// 
    //   this.evaluatedCustomer = this.groupName + '-' +this.evaluatedCustomer;
    //   this.evaluateInformation = this.groupName + '-' +this.evaluateInformation;   
    // }
    if (this.routerInfo.snapshot.params['custNo']) {
      this.custNo = this.routerInfo.snapshot.params['custNo'];//  
    }
    this.selectCustGroupInfo(this.groupId, this.custNo);

    if (this.routerInfo.snapshot.params['custGroupView']) {
      this.custGroupView = this.routerInfo.snapshot.params['custGroupView'];//  
      if (this.custGroupView == '1') {
        this.isCustGroupView = true;
      }
    }
    if (this.routerInfo.snapshot.params.pageNum) {
      this.pageNum = this.routerInfo.snapshot.params.pageNum;
    }
    // if (this.routerInfo.snapshot.params['customInfo']) {
    //   let tempCustomInfo = this.routerInfo.snapshot.params['customInfo'];
    //   this.custGroupListBean.custMap = JSON.parse(tempCustomInfo);
    // }

    this.groupEvalInfoBean.groupId = _.clone(this.groupId);
    this.groupEvalInfoBean.custNo = _.clone(this.custNo);
    this.groupEvalInfoBean.handleFlag = '2';
    this.groupEvalInfoBean.pageNum = 1;
    this.groupEvalInfoBean.pageSize = 999;
  }
  // createForm() {
  //   this.myForm = this.fb.group({
  //     mobile: ['', [Validators.required, checkTel]]
  //   })
  // };
  // postDate() {
  //   if (this.myForm.valid) {
  //   }
  // }
  isQl(col) {
    if (col.custNo.search(/^QL/)) {
      return true;
    }
    return false;
  }
  //
  selectCustGroupInfo(groupId, custNo) {
    let params = {
      groupId: groupId
    }
    this.httpService.selectCustGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupName = data.groupName;
        this.evaluatedCustomer = this.groupName + '-' + this.evaluatedCustomer;
        this.evaluateInformation = this.groupName + '-' + this.evaluateInformation;
        this.groupEvalInfoBean.groupName = _.clone(this.groupName);

        this.selectCustGroupList(custNo);//
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' })
    })
  }
  //
  selectCustGroupList(custNo) {
    let params = {
      custNo: custNo,
      groupId: this.groupId,
      statusId: 1
    }
    this.httpService.selectCustGroupList(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        console.log(data.custList)
        if (data.custList && data.custList.length != 0) {
          this.custGroupListBean.custMap = data.custList[0];
          if (this.custGroupListBean.custMap.familyIncome) {
            this.custGroupListBean.custMap.familyIncome = parseInt(this.custGroupListBean.custMap.familyIncome)
          }
          if (this.custGroupListBean.custMap.familyAss) {
            this.custGroupListBean.custMap.familyAss = parseInt(this.custGroupListBean.custMap.familyAss)
          }

          if (this.isQl(this.custGroupListBean.custMap)) {//
            this.custNoTemp = this.custGroupListBean.custMap.custNo;
          } else {
            this.custNoTemp = '';
          }

          data.custList.forEach((item, i) => {
            this.systemCreditFlag = item.systemCreditFlag;
            this.agentCreditFlag = item.agentCreditFlag;
          });
        }

      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' })
    })
  }
  selectBtn() {
    // 

    this.custGroupListDisplay = true;
    // this.selectGroupEvalInfo();
    // this.selectGroupAdminInfo();
    this.inVal = this.groupId;
  }
  selectGroupAdminInfo() {
    // 
    this.groupAdminInfoBean.groupId = _.clone(this.groupId);
    this.groupAdminInfoBean.roleType = '4';
    this.groupAdminInfoBean.statusId = '1';
    this.httpService.selectGroupAdminInfo(this.groupAdminInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupAdminInfoBean = data;
        this.groupAdminInfoBean.roleList = data.roleList;
        this.inVal = this.groupAdminInfoBean.roleList;
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }


  selectGroupEvalInfo() {
    //
    this.httpService.selectGroupEvalInfo(this.groupEvalInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.evalList && data.evalList.length != 0) {
          this.groupEvalInfoBean.evalList = data.evalList;
        } else {
          setTimeout(() => {
            this.groupEvalInfoBean.evalList = [];
          })
        }
        // if(this.groupEvalInfoBean.evalList && this.groupEvalInfoBean.evalList.length > 0){
        //   this.agentCreditFlag = '1';
        //   this.systemCreditFlag = '1';
        // }else{
        //   this.agentCreditFlag = '';
        //   this.systemCreditFlag = '';
        // }
        // this.groupEvalInfoBean.evalList.forEach(item => {
        //   if(item.healthFlag == "20" ||item.creditEval == "3" ||item.creditEval == "4" ||item.badHabits == "10" ||item.badHabits == "20" 
        //     ||item.familyStableFlag == "20" ||item.offerLines == "60"){
        //       this.agentCreditFlag = '0'
        //       this.systemCreditFlag = '0'
        //   }

        // })
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }
  closeGroupEvalInfo(event) {
    // 
    // this.groupEvalInfoBean = new GroupEvalInfoBean();//
    this.custGroupListDisplay = event.custGroupListDisplay;
    let list = event.roleList;
    let temArr = [];
    temArr = _.clone(this.groupEvalInfoBean.evalList);
    let returnFlag = false;
    list.forEach((item, i) => {
      let eachFlag = false;
      this.groupEvalInfoBean.evalList.forEach(item1 => {
        if (item1.sysCreditEval == item.userId) {
          returnFlag = true;
          eachFlag = true;
        }
      })
      if (!eachFlag) {
        temArr.push({ evalName: item.userName, sysCreditEval: item.userId });
      }
    })
    if (returnFlag) {
      this.msgs = [];
      this.msgs = [{ severity: 'info', summary: '', detail: '' }];
    }
    this.groupEvalInfoBean.evalList = _.clone(temArr);
    // this.groupAdminInfoBean = new GroupAdminInfoBean();
  }
  // 
  update() {
    //
    if (this.groupEvalInfoBean.evalList.length == 0) {
      // this.groupEvalInfoBean.evalList['partyId'] = this.custNo;     
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
      return;
    } else {
      let flag1 = true;
      flag1 = this.isNull();
      if (!flag1) {
        return;
      }
    }
    //
    if (!this.infoEditFlag) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
      return false;
    }
    //()
    if (this.groupEvalInfoBean.evalList.length >= 2) {
      if (!this.systemCreditFlag || !this.agentCreditFlag || this.systemCreditFlag == '' || this.agentCreditFlag == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        return false;
      } else {
        this.systemJudge(2);
      }
    }
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        this.updateCustGroupList(2);//
        this.updateGroupEvalInfo();
      }
    });
  }
  updateGroupEvalInfo() {
    // 
    this.groupEvalInfoBean.evalList.forEach((item, i) => {
      this.groupEvalInfoBean.evalList[i]['partyId'] = _.clone(this.custNo);
      this.groupEvalInfoBean.evalList[i]['fromDate'] = null;
    })

    this.httpService.updateGroupEvalInfo(this.groupEvalInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '', detail: '' }];
        this.selectGroupEvalInfo();
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }
  deleteGroupEvalInfo(index) {
    // if (this.groupEvalInfoBean.evalList.length == 1) {
    //   // this.groupEvalInfoBean.evalList['partyId'] = this.custNo;     
    //   this.msgs = [];
    //   this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    // } else {
    // /
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        // debugger
        let temArr = _.clone(this.groupEvalInfoBean.evalList);
        temArr.splice(index, 1);
        this.groupEvalInfoBean.evalList = _.clone(temArr);
      }
    });
    // }

  }
  // 
  returnCustGroupView() {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName, pageNum: this.pageNum }]);
      }
    });
  }

  editInfo() {
    // 
    this.infoEditFlag = false;
  }

  // 
  saveInfo() {
    //-
    if (!this.custGroupListBean.custMap.advise || this.custGroupListBean.custMap.advise == '') {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
      return false;
    }
    this.infoEditFlag = true;
    this.updateCustGroupList(1);
  }
  updateCustGroupList(i) {
    this.custGroupListBean.groupId = this.groupId;
    this.custGroupListBean['custMap']['systemCreditFlag'] = this.systemCreditFlag;
    this.custGroupListBean['custMap']['agentCreditFlag'] = this.agentCreditFlag;
    this.custGroupListBean['flagId'] = '1';
    // 
    this.httpService.updateCustGroupList(this.custGroupListBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (i == '1') {
          this.msgs = [];
          this.msgs = [{ severity: 'success', summary: '', detail: '' }];
        }
        this.selectCustGroupList(this.custNo);//
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }

  isNull() {
    //-
    if (!this.custGroupListBean.custMap.advise || this.custGroupListBean.custMap.advise == '') {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
      return false;
    }
    // 
    if (!this.groupEvalInfoBean.evalList || this.groupEvalInfoBean.evalList.length == 0) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
      return false;
    }
    let flag = true;
    this.groupEvalInfoBean.evalList.forEach(item => {
      if (!item.evalName || item.evalName == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        flag = false;
        return;
      }
      if (!item.healthFlag || item.healthFlag == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        flag = false;
        return;
      }
      if (!item.creditEval || item.creditEval == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        flag = false;
        return;
      }
      if (!item.badHabits || item.badHabits == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        flag = false;
        return;
      }
      if (!item.familyStableFlag || item.familyStableFlag == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        flag = false;
        return;
      }
      if (!item.offerLines || item.offerLines == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        flag = false;
        return;
      }
    })
    if (!flag) {
      return false;
    }
    return true;
  }
  //
  systemJudge(isButton) {
    if (this.groupEvalInfoBean.evalList && this.groupEvalInfoBean.evalList.length > 0) {
      this.isNull()
      //
      if (isButton == '1') {
        if (this.groupEvalInfoBean.evalList.length == 1) {
          this.agentCreditFlag = '0';
          this.systemCreditFlag = '0';
        } else {
          this.agentCreditFlag = '1';
          this.systemCreditFlag = '1';
        }
      }

      //
      this.groupEvalInfoBean.evalList.forEach(item => {
        if (item.healthFlag == "20" || item.creditEval == "3" || item.creditEval == "4" || item.badHabits == "2" || item.badHabits == "3"
          || item.familyStableFlag == "20" || item.offerLines == "90" || item.offerLines == "100") {
          this.systemCreditFlag = '0';
          this.agentCreditFlag = '0';
        }
      })

      //
      if (this.custGroupListBean.custMap.advise == "10" || this.custGroupListBean.custMap.advise == "330") {
        this.systemCreditFlag = '0';
        this.agentCreditFlag = '0';
      }
    } else {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    }
  }
  typeChange(event) {

  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
